<template>
  <div class="lading">
    <div class="tab">
      <span @click="back()"></span>>
      <span v-show='!this.$route.query.blId'></span>
      <span v-show='this.$route.query.blId'></span>
    </div>
    <div class="basic">
      <h3></h3>
      <ul class="clearfix">
        <li>
          <em> :</em>
          <span>{{taskProfile.nomineeCompany }}</span>
        </li>
        <li>
          <em> :</em>
          <span>{{taskProfile.purchaseContractNo}}</span>
        </li>
        <li>
          <em> :</em>
          <span>{{taskProfile.quantity}}</span>
        </li>
        <li>
          <em> :</em>
          <span>{{taskProfile.category}}</span>
        </li>
        <li>
          <em> :</em>
          <span>{{taskProfile.specification}}</span>
        </li>
        <li>
          <em> :</em>
          <span>{{taskProfile.laycan}}</span>
        </li>
      </ul>
    </div>
    <div class="tag clearfix">
      <div @click="storage()"
           v-show="zancun">
        <el-tag>
          <i class="iconfont icon-security"></i></el-tag>
      </div>
      <router-link target="_blank"
                   :to="{path:'/printBl',query: {blId: this.$route.query.blId,id:this.$route.query.id,print:1}}">
        <div>
          <el-tag>
            <i class="iconfont icon-security"></i> </el-tag>
        </div>
      </router-link>
      <div @click="submitForm('ruleForm')">
        <el-tag>
          <i class="iconfont icon-security"></i></el-tag>
      </div>
      <router-link :to="{path:'/upload',query: {blId: this.$route.query.blId,sofId:this.$route.query.sofId,taskId:this.$route.query.id}}">
        <div>
          <el-tag>
            <i class="iconfont icon-security"></i> </el-tag>
        </div>
      </router-link>
    </div>
    <el-form :model="ruleForm"
             :rules="rules"
             ref="ruleForm"
             class="clearfix"
             @validate="rule"
             :show-message=false>
      <table border="1px"
             cellspacing="0px"
             style="table-layout:fixed;border-collapse:collapse;"
             class="table">
        <tr>
          <th colspan="8">
            <el-form-item class="fr"
                          prop='blNo'>
              <el-input v-model="ruleForm.blNo"></el-input>
            </el-form-item>
            <em class="fr">*B/L NO:</em>TANKER BILL OF LADING
          </th>
        </tr>
        <tr>
          <td colspan="3"
              class="bg">*Shipped on board in apparent good order and condition by (Consignor)</td>
          <td colspan="5">
            <el-form-item prop="consignor">
              <el-input v-model="ruleForm.consignor"></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td class="bg">*on board the tanker</td>
          <td colspan="2">
            <el-form-item prop="vessel">
              <el-input v-model="ruleForm.vessel"
                        type="textarea"
                        :autosize="{ minRows: 1, maxRows: 3}"></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="2">*Shipped on board</td>
          <td colspan="3">
            <el-form-item prop="loadingPort">
              <el-input v-model="ruleForm.loadingPort"
                        type="textarea"
                        :autosize="{ minRows: 1, maxRows: 3}"></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td class="bg">*Where of</td>
          <td colspan="2">
            <el-form-item prop="unloadingPort">
              <el-input v-model="ruleForm.unloadingPort"
                        type="textarea"
                        :autosize="{ minRows: 1, maxRows: 3}"></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="2">*Is the Master, to be delivered to the port of discharge</td>
          <td colspan="3">
            <el-form-item prop="captain">
              <el-input v-model="ruleForm.captain"
                        type="textarea"
                        :autosize="{ minRows: 1, maxRows: 3}"></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td class="bg"
              colspan="2">*Consignee</td>
          <td colspan="6">
            <el-form-item prop="consignee">
              <el-input v-model="ruleForm.consignee"></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td class="bg"
              colspan="2">*Notify</td>
          <td colspan="6">
            <el-form-item prop="notify">
              <el-input v-model="ruleForm.notify"></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td colspan="3"
              rowspan="6">
            <el-form-item prop="category">
              <el-input v-model="ruleForm.category"
                        placeholder=""></el-input>
            </el-form-item>
          </td>
          <td colspan="5"
              class="bg"
              align="center">QUANTITY</td>
        </tr>
        <tr>
          <td colspan="3">
            <el-form-item prop="metricTon">
              <el-input v-model="ruleForm.metricTon"></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="2">*MT</td>
        </tr>
        <tr>
          <td colspan="3">
            <el-form-item prop="longTon">
              <el-input v-model="ruleForm.longTon"></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="2">*LT</td>
        </tr>
        <tr>
          <td colspan="3">
            <el-form-item prop="bbls">
              <el-input v-model="ruleForm.bbls"></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="2">*US BBLS 60F</td>
        </tr>
        <tr>
          <td colspan="3">
            <el-form-item prop="ltrs">
              <el-input v-model="ruleForm.ltrs"></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="2">*LTRS AT 15C</td>
        </tr>
        <tr>
          <td colspan="3">
            <el-form-item prop="ltrsObsTrmp">
              <el-input v-model="ruleForm.ltrsObsTrmp"></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="2">*LTRS AT OBS TRMP</td>
        </tr>
        <tr>
          <td colspan="8">
            <el-form-item prop="remark">
              <el-input v-model="ruleForm.remark"
                        placeholder=""></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td colspan="8"
              class="pd20 color">The quantity, measurement, weight, gauge, quality nature and value and actual condition of the cargo unknown to the Vessel and the Master to be delivered at the port of discharge or so near thereto as the Vessel can safely get, always afloat upon prior payment of freight as agreed.</td>
        </tr>
        <tr>
          <td colspan="3"
              class="bg">*This shipment is carried under and pursuant to the terms of the Charter dated</td>
          <td colspan="5">
            <el-form-item prop="cpDate1">
              <el-input v-model="ruleForm.cpDate1"
                        placeholder="Month/Day/Year"></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td class="bg"
              colspan="2">*at</td>
          <td colspan="2">
            <el-form-item prop="cpDate2">
              <el-input v-model="ruleForm.cpDate2"></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="2">*Between</td>
          <td colspan="2">
            <el-form-item prop="cpDate3">
              <el-input v-model="ruleForm.cpDate3"></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td class="bg"
              colspan="2">*and</td>
          <td colspan="6">
            <el-form-item prop="cpDate4">
              <el-input v-model="ruleForm.cpDate4"></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td colspan="8"
              class="pd20 color">as Charterer, and all the terms whatsoever of the said Charter except the rate and payment of freight specified therein apply to and govern the rights of the parties concerned in this shipment. Copy of the Charter may be obtained from the Shipper or Charterer. If this Bill of Lading is a document of title to which the Carriage of Goods by Sea Act of the United Sates, approved April 16, 1936, or similar legislation giving statutory effect to the International convention for the Unification of Certain Rules relating to Bills of Lading at Brussels of August 25, 1924, applies by reason of the port of loading or discharge being in territory in which the said Act or other similar legislation is in force, this Bill of Lading shall have effect subject to the provisions of the said Act or other similar legislation, as the case may be, which shall be deemed incorporated herein, and nothing herein contained shall be deemed a surrender by the carrier of any of its rights or immunities or an increase of any of its responsibilities or liabilities under said Act or other similar legislation. If any term of this Bill of Lading is repugnant to the said Act or other similar legislation as so incorporated, such terms shall be void to that extent but no further. The contract of carriage evidenced by this Bill of Lading is between the shipper, consignee and/or owner of the cargo and the owner or demise chartered of the vessel named herein to carry the cargo described above. It is understood and agreed that, other than said shipowner or demise charterer, no person, firm or corporation or other legal entity whatsoever, is or shall be deemed to be liable with respect to the shipment as carrier, bailee or otherwise in contract or in tort. If, however, it shall be adjudged that any other than said shipowner or demise charterer is carrier or bailee of said shipment or under any responsibility with respect thereto, all limitations of or exonerations from liability and all defenses provided by law or by the terms of the contract of carriage shall be available to such other. </td>
        </tr>
        <tr>
          <td colspan="4"
              class="bg">*In witness where of, the Master has signed</td>
          <td colspan="4">
            <el-form-item prop="signNum">
              <el-input v-model="ruleForm.signNum"></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td colspan="8"
              class="pd20 color">Bills of Lading of this tenor and date, one of which being accomplished, the others will be void.</td>
        </tr>
        <tr>
          <td class="bg">*Dated at</td>
          <td colspan="1">
            <el-form-item prop="timeZone">
              <el-input v-model="ruleForm.timeZone"
                        type="textarea"
                        autosize></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="1">*this</td>
          <td colspan="1">
            <el-form-item prop="timeDay">
              <el-input v-model="ruleForm.timeDay"></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="1">*Day of</td>
          <td colspan="1">
            <el-form-item prop="timeMonth">
              <el-input v-model="ruleForm.timeMonth"></el-input>
            </el-form-item>
          </td>
          <td class="bg"
              colspan="1">* ,</td>
          <td colspan="1">
            <el-form-item prop="timeYear">
              <el-input v-model="ruleForm.timeYear"></el-input>
            </el-form-item>
          </td>
        </tr>
        <tr>
          <td class="bg"
              colspan="2">*MASTER OF</td>
          <td colspan="6">
            <el-form-item prop="masterName">
              <el-input v-model="ruleForm.masterName"></el-input>
            </el-form-item>
          </td>
        </tr>
      </table>
    </el-form>
  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'lading',
  data () {
    return {
      zancun: true,
      taskProfile: {},
      ruleForm: {
        taskId: this.$route.query.id,
        blNo: '',
        consignor: '',
        vessel: '',
        loadingPort: '',
        captain: '',
        unloadingPort: '',
        consignee: '',
        notify: '',
        category: '',
        metricTon: '',
        longTon: '',
        bbls: '',
        ltrs: '',
        ltrsObsTrmp: '',
        remark: '',
        cpDate1: '',
        cpDate2: '',
        cpDate3: '',
        cpDate4: '',
        signNum: '',
        timeZone: '',
        timeDay: '',
        timeMonth: '',
        timeYear: '',
        masterName: ''
      },
      rules: {
        blNo: [
          { required: true, message: '', trigger: 'blur' }
        ],
        consignor: [
          { required: true, message: '', trigger: 'blur' }
        ],
        vessel: [
          { required: true, message: '', trigger: 'blur' }
        ],
        loadingPort: [
          { required: true, message: '', trigger: 'blur' }
        ],
        captain: [
          { required: true, message: '', trigger: 'blur' }
        ],
        unloadingPort: [
          { required: true, message: '', trigger: 'blur' }
        ],
        consignee: [
          { required: true, message: '', trigger: 'blur' }
        ],
        notify: [
          { required: true, message: '', trigger: 'blur' }
        ],
        category: [
          { required: true, message: '', trigger: 'blur' }
        ],
        metricTon: [
          { required: true, message: '', trigger: 'blur' }
        ],
        longTon: [
          { required: true, message: '', trigger: 'blur' }
        ],
        bbls: [
          { required: true, message: 'bbls', trigger: 'blur' }
        ],
        ltrs: [
          { required: true, message: 'ltrs', trigger: 'blur' }
        ],
        ltrsObsTrmp: [
          { required: true, message: 'ltrsObsTrmp', trigger: 'blur' }
        ],
        cpDate1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        cpDate2: [
          { required: true, message: '', trigger: 'blur' }
        ],
        cpDate3: [
          { required: true, message: '', trigger: 'blur' }
        ],
        cpDate4: [
          { required: true, message: '', trigger: 'blur' }
        ],
        signNum: [
          { required: true, message: '', trigger: 'blur' }
        ],
        timeZone: [
          { required: true, message: '', trigger: 'blur' }
        ],
        timeDay: [
          { required: true, message: '', trigger: 'blur' }
        ],
        timeMonth: [
          { required: true, message: '', trigger: 'blur' }
        ],
        timeYear: [
          { required: true, message: '', trigger: 'blur' }
        ],
        masterName: [
          { required: true, message: 'masterName', trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
  },
  created () {
    this.profile()
    if (this.$route.query.blId) {
      // this.zancun = false
      this.billDetail()
    }
  },
  methods: {
    // 
    profile () {
      this.ajax(
        url.taskProfile,
        { taskId: this.$route.query.id },
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.taskProfile = res.data.data
        } else {
          this.$message(res.data.message)
        }
      })
    },
    back () {
      this.$router.go(-1)
    },
    // 
    rule (e, t) {
      if (!t) {
        this.$message.error(this.rules[e][0].message)
      }
    },
    // 
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.submit()
        } else {
          return false
        }
      })
    },
    // 
    submit () {
      this.ajax(
        url.billSubmit,
        this.ruleForm,
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.$message('')
        } else {
          this.$message(res.data.message)
        }
      })
    },
    // 
    storage () {
      this.ajax(
        url.billStorage,
        this.ruleForm,
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.$message('')
        } else {
          this.$message(res.data.message)
        }
      })
    },
    // sof
    billDetail () {
      this.ajax(
        url.billDetail,
        { id: this.$route.query.blId },
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.ruleForm = res.data.data
          if (res.data.data.status === '2') {
            this.zancun = false
          }
          // console.log(this.ruleForm)
        } else {
          this.$message(res.data.message)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.color {
  color: #9ea0a5;
}
.pd20 {
  padding: 20px;
}
.bg {
  background: #f5f6f7;
  padding: 4px;
  color: #333333;
}
.el-form-item {
  margin-bottom: 0px;
}
.lading {
  .basic {
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.04);
    border: 1px solid rgba(234, 237, 243, 1);
    padding: 30px;
    h3 {
      font-size: 20px;
      font-weight: 500;
      color: rgba(62, 63, 66, 1);
    }
    li {
      float: left;
      width: 33%;
      padding-top: 15px;
      em {
        width: 80px;
        float: left;
        text-align: right;
        padding-right: 10px;
        color: #666666;
      }
      span {
        float: left;
        color: #333333;
      }
    }
  }
  .tag {
    padding: 20px 0;
    background: #fbfbfd;
    div {
      float: left;
      span{
       cursor:pointer;
      }
    }
    .el-tag {
      box-shadow: 0px 1px 1px 0px rgba(22, 29, 37, 0.05),
        0px 2px 0px 0px rgba(255, 255, 255, 0.05);
      border-radius: 4px;
      border: 1px solid rgba(216, 220, 230, 1);
      width: 104px;
      height: 44px;
      line-height: 44px;
      text-align: center;
      margin-right: 20px;
      color: rgba(62, 63, 66, 1);
      i {
        color: rgba(216, 220, 230, 1);
        padding-right: 6px;
      }
    }
    .el-tag:hover {
      background: linear-gradient(
        360deg,
        rgba(232, 233, 235, 1) 0%,
        rgba(239, 240, 242, 1) 100%
      );
    }
  }
  .form {
    border: 1px solid #eaedf3;
    padding: 20px;
    margin-bottom: 20px;
  }
  .table {
    margin: 20px 0;
    width: 100%;
    font-size: 12px;
    th {
      border: 1px solid #eaedf3;
      text-align: left;
      line-height: 40px;
      padding: 20px 0 20px 20px;
      em {
        padding-right: 10px;
      }
    }
    td {
      border: 1px solid #eaedf3;
      width: 16%;
    }
  }
}
</style>



